<template>
  <div class="page">
    <div flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <img class="user-avatar" :src='userInfo.user.avatar_file||avatar' style='width:2.5rem;height:2.5rem;'>
      </div>
      <div flex-box="1">
        <div class="mb-10">:<span v-text='userInfo.user.phone'></span></div>
        <div class="t-xs">
          ACI<br>{{courseCount}}
        </div>
      </div>
    </div>
    <!-- <div v-if='userInfo.status==0&&courseCount>0' flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <i style='color:#7ED321;font-size:2rem;' class="iconfont icon-caifang-tianbaoren"></i>
      </div>
      <div flex-box="1">
        <div class="mb-10"></div>
        <div class="t-xs mb-10">
          ACI
        </div>
        <div class="t-xs">
          <mt-button type='danger' @click.native='$router.push("./info")' size='small'></mt-button>
        </div>
      </div>
    </div> -->
    <div v-if='!courseCount' flex class="mb-20 user-info">
      <div flex-box="0" style='margin-right:1rem;'>
        <i style='color:#FF5900;font-size:2rem;' class="iconfont icon-course"></i>
      </div>
      <div flex-box="1">
        <div class="mb-10"></div>
        <div class="t-xs mb-10">
          ACI
        </div>
        <div class="t-xs">
          <mt-button type='danger' @click.native='$router.push("./male")' size='small'></mt-button>
        </div>
      </div>
    </div>
    <div class="mb-20">
      <!-- <mt-cell title="" v-if='userInfo.status==3' @click.native='$router.push("./signup")' is-link>
        <span style="color: green"></span>
      </mt-cell> -->
      <mt-cell @click.native='courseCount?$router.push("./live"):$toast("")' title="" is-link>
        <span></span>
      </mt-cell>
      <mt-cell @click.native='courseCount?$router.push("./courseList"):$toast("")' title="" is-link>
        <span>{{courseCount}}</span>
      </mt-cell>
      <mt-cell title="" :to='"tel:"+tel' is-link>
        <span>{{tel}}</span>
      </mt-cell>
    </div>
    <div class="t-center user-logout">
      <mt-button @click.native='doLogout' style="width:60vw;"></mt-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      tel: "010-51657777",
      avatar: require("../../assets/avatar.png")
    };
  },
  methods: {
    ...mapActions({
      getUserInfo: "getUserInfo",
      getCourseCount: "getCourseCount"
    }),
    doLogout() {
      this.$storage.remove("userToken");
      this.$router.push("/login");
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo,
      courseCount: state => state.courseCount
    })
  },
  mounted() {
    this.getUserInfo();
    this.getCourseCount();
  }
};
</script>
<style lang="less" scoped>
.page {
  padding: 40px 0 50px;
  //height: 100vh;
  //box-sizing: border-box;
}
.user-info {
  padding: 1rem;
  background-color: white;
  .user-avatar {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 2.5rem;
  }
}
.user-logout {
  //position: fixed;
  margin: 2rem 0;
  width: 100vw;
  bottom: 3rem;
}
</style>



